<template>
<el-calendar>
  <!-- 这里使用的是 2.5 slot 语法，对于新项目请使用 2.6 slot 语法-->
  <template
    slot="dateCell"
    slot-scope="{date, data}">
    <el-tooltip class="item" effect="dark" v-bind:content="getTodoOfDay(date)" placement="top">
      <p :class="data.isSelected ? 'is-selected' : ''">
      {{data.day.split('-')[2]}} <br/>{{getTodoOfDay(date)}}
    </p>
    </el-tooltip>

  </template>
</el-calendar>
</template>

<script>
export default {
  name: 'Calendar',
  data: function() {
    return {
      items: [],
    }
  },
  methods: {
    getTodoOfDay(date) {
      const events = [];
      for (let item of this.items) {
        const due = new Date(item.due_date);
        if(date.getYear() === due.getYear()
          && date.getMonth() === due.getMonth()
          && date.getDate() === due.getDate()
        ) {
          events.push(
            item.title
          );
        }
      }
      return events.join(';')
    }
  },
  mounted: function () {
    console.log("mounted");
    fetch("/api/item_list")
      .then((r) => r.json())
      .then((r) => {
        this.items = r.data;
      });
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.el-calendar-day {
  overflow: hidden;
}
@media screen and (max-width: 525px) {
.el-calendar{
  font-size: 10px;
}}

</style>
